<template>
  <div>
  <el-form id="pdfDom" label-width="80px">
    <el-form-item :ref="form" label="姓名" :model="form">
      <el-col :span="6">
        <el-input v-model="form.name" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="员工编号">
      <el-col :span="6">
        <el-input v-model="form.staffnum" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="身份证号">
      <el-col :span="6">
        <el-input v-model="form.id" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="生日">
      <el-col :span="5">
        <el-input v-model="form.birthday" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="TEL">
      <el-col :span="6">
        <el-input v-model="form.tel" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="Email">
      <el-col :span="10">
        <el-input v-model="form.email" width=5 :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="职称">
      <el-col :span="10">
        <el-input v-model="form.job" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="办公室">
      <el-col :span="10">
        <el-input v-model="form.office" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="擅长领域">
      <el-col :span="10">
        <el-input v-model="form.specialization" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="住址">
        <el-col :span="10">
        <el-input v-model="form.address" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
  </el-form>
      <el-button type="primary" @click="onChange" v-if="bt1">更改</el-button>
      <el-button @click="onSubmit" v-if="bt2">提交</el-button>
      <el-button @click="onCancel">取消</el-button>
      <el-button @click="getPdf()">打印个人资料</el-button>
  </div>
</template>
<script>
// const url = 'http://localhost:8080/userinfo'
export default {
  data () {
    return {
      form: {
        // accountid: This.$store.state.accountid,
        name: '',
        staffnum: '',
        id: '',
        birthday: '',
        tel: '',
        email: '',
        job: '',
        office: '',
        specialization: '',
        address: ''
        // accounttype: 'researcher'
      },
      bt1: true,
      bt2: false,
      edit: true,
      htmlTitle: 'userinfo'
    }
  },
  mounted () {
    console.log('get userinfo')
    this.loadData(this.setData)
  },
  methods: {
    onChange () {
      this.bt1 = false
      this.bt2 = true
      this.edit = false
    },
    onSubmit () {
      console.log('submit!')
      // var router = this.$router
      const This = this
      let form = this.form
      this.$axios({
        method: 'post',
        url: 'http://localhost:8088/userinfo',
        data: {
          accountid: This.$store.state.accountid,
          name: form.name,
          staffnum: form.staffnum,
          id: form.id,
          birthday: form.birthday,
          tel: form.tel,
          email: form.email,
          job: form.job,
          office: form.office,
          specialization: form.specialization,
          address: form.address,
          accounttype: 'researcher'
        }
      }).then(function (response) {
        console.log(response.data)
        if (response.data.code === 400) {
          This.bt1 = true
          This.bt2 = false
          This.edit = true
        }
      }).catch(function (error) {
        console.log(error)
        alert('更改失败')
      })
    },
    onCancel () {
      this.$router.go(-1)
    },
    loadData (callBack) {
      this.getUserinfoPromise(this.$store.state.accountid)
        .then(res => {
          callBack(res)
        })
        .catch(res => {
          console.log(res)
        })
    },
    getUserinfoPromise (id) {
      return new Promise((resolve, reject) => {
        this.$axios.get('http://localhost:8088/userinfo?accountid=' + id)
          .then(res => {
            resolve(res)
          })
          .catch(res => {
            reject(res)
          })
      })
    },
    setData (data) {
      console.log(data.data.data)
      if (data.data.data == null) {
        console.log('userinfo is null')
        this.onChange()
      } else {
        this.form = data.data.data
      }
      // this.form = data.data.data
    }
  }
}
</script>



