import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    accountid: 0,
    accountname: 'testuser',
    proj: []
  },
  mutations: {
    newAccountid (state, msg) {
      state.accountid = msg
    },
    newAccountname (state, msg) {
      state.accountname = msg
    },
    newProj (state, msg) {
      state.proj = msg
    }
  }
})
export default store



