<template>
  <el-container>
    <el-header style="background-color: white; height: 40px">
    <el-menu
      :default-active="activeIndex2"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b">
      <el-menu-item index="1" @click="showIssues">Issues</el-menu-item>
      <el-submenu index="2">
        <template slot="title">科研项目管理</template>
        <el-menu-item index="2-1">所有项目</el-menu-item>
        <el-menu-item index="2-2">横向项目</el-menu-item>
        <el-menu-item index="2-3">纵向项目</el-menu-item>
      </el-submenu>
      <el-menu-item index="3">科研成果管理</el-menu-item>
      <el-menu-item index="4">用户管理</el-menu-item>
    </el-menu>
    </el-header>
    <el-header style="text-align: right; font-size: 40px; background-color: white; height: 55px">
      <el-dropdown>
      <i class="el-icon-setting" style="margin-right: 20px; color: black">{{this.$store.state.accountname}}</i>
      <el-dropdown-menu slot="dropdown">
      <el-dropdown-item @click.native="toLogOut">Log Out</el-dropdown-item>
      </el-dropdown-menu>
      </el-dropdown>
    </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  <!--</el-container>-->
  <!--</el-container>-->
</template>

<script>
export default {
  name: 'AdminMainPage',
  methods: {
    showIssues () {
      this.$router.push('/admin/' + this.$store.state.accountname)
    },
    toLogOut () {
      this.$router.push('/')
    }
  }
}
</script>

<style scoped>

</style>



