<template id="resLoginTemp">
  <div style="margin: 60px">
  <div class="module form-module">
    <div>
      <el-tabs type="border-card">
        <el-tab-pane label="登陆">
          <div style="margin: 20px; margin-right: 40px">
            <el-form label-width="80px" >
              <el-form-item  label="用户名">
                <el-input v-model="signin.username"></el-input>
              </el-form-item>
              <el-form-item  label="密码">
                <el-input type="password" v-model="signin.password"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSignin">登陆</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-tab-pane>
        <el-tab-pane label="注册">
          <div style="margin: 20px; margin-right: 40px">
            <el-form :model="signup" :rules="rules" ref="signup" label-width="80px" >
              <el-form-item  label="用户名" prop="username">
                <el-input v-model="signup.username"></el-input>
              </el-form-item>
              <el-form-item label="密码" prop="passwd1">
                <el-input type="password" v-model="signup.passwd1" ></el-input>
              </el-form-item>
              <el-form-item label="密码确认" prop="passwd2">
                <el-input type="password" v-model="signup.passwd2"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSignup">注册</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
  </div>
</template>
<script>
module.exports = {
  data: function () {
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.signup.passwd2 !== '') {
          this.$refs.signup.validateField('passwd2')
        }
        callback()
      }
    }
    let validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入确认密码'))
      } else if (value !== this.signup.passwd1) {
        callback(new Error('密码不一致'))
      } else {
        callback()
      }
    }
    return {
      signin: {
        username: '',
        passwd: ''
      },
      signup: {
        username: '',
        passwd1: '',
        passwd2: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 15, message: '长度在3到15之间', trigger: 'blur' }
        ],
        passwd1: [
          { validator: validatePass, trigger: 'blur' },
          { min: 6, max: 15, message: '长度在6到15之间', trigger: 'blur' }
        ],
        passwd2: [
          { validator: validatePass2, trigger: 'blur' },
          { min: 6, max: 15, message: '长度在6到15之间', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    onSignin: function () {
      let router = this.$router
      let store = this.$store
      let signin = this.signin
      this.$axios({
        method: 'post',
        url: 'http://localhost:8088/account/signin',
        data: {
          id: null,
          accountname: signin.username,
          passwd: signin.password,
          regtime: null
        }
      }).then(function (response) {
        console.log(response.data)
        console.log(response.data)
        if (response.data.code === 200) {
          router.push('/researchers/' + signin.username)
          store.commit('newAccountid', response.data.data.id)
          store.commit('newAccountname', response.data.data.accountname)
          console.log(store.state.accountid + store.state.accountname)
        }
      })
        .catch(function (error) { console.log(error) })
    },
    onSignup: function () {
      let router = this.$router
      let signup = this.signup
      this.$axios({
        method: 'post',
        url: 'http://localhost:8088/account/signup',
        data: {
          id: null,
          accountname: signup.username,
          passwd: signup.passwd1,
          regtime: null
        }
      }).then(function (response) {
        console.log(response.data)
        console.log(response.data)
        if (response.data.code === 200) {
          router.push('/')
        }
      })
        .catch(function (error) { console.log(error) })
    }
  }
}
</script>

<style scoped>
  .form-module {
    position: relative;
    background: #ffffff;
    width: 400px;
    border-top: 2px solid #33b5e5;
    -webkit-box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
    margin: 0 auto;
  }
</style>



